import { BaseProduct } from '../types/scraper';
import { Platform } from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';

export class ProductService {
  private static isWeb = Platform.OS === 'web';
  private static DATA_PATH = '/public/data/foils.json';
  private static CACHE_DIR = '.cache';

  static async getAllProducts(): Promise<BaseProduct[]> {
    try {
      if (this.isWeb) {
        // Try loading from public/data first
        try {
          const response = await fetch(this.DATA_PATH);
          if (response.ok) {
            const products = await response.json();
            console.log(`Loaded ${products.length} products from ${this.DATA_PATH}`);
            return products;
          } else {
            console.warn(`Failed to load from ${this.DATA_PATH} (${response.status}), trying .cache directory...`);
          }
        } catch (error) {
          console.warn(`Failed to load from ${this.DATA_PATH}, trying .cache directory...`, error);
        }

        // Try loading from .cache as fallback
        try {
          const brands = ['uni-foil', 'code-foils', 'axis-foils', 'kt-foils', 'lift-foils', 'armstrong-foils'];
          const allProducts: BaseProduct[] = [];
          
          for (const brand of brands) {
            try {
              const response = await fetch(`/${this.CACHE_DIR}/${brand}.json`);
              if (response.ok) {
                const products = await response.json();
                console.log(`Loaded ${products.length} products for ${brand} from .cache`);
                allProducts.push(...products);
              }
            } catch (error) {
              console.warn(`No cache file found for ${brand}`);
            }
          }

          if (allProducts.length > 0) {
            return allProducts;
          }
        } catch (error) {
          console.warn('Failed to load from .cache directory', error);
        }
      } else {
        // For mobile, use AsyncStorage
        const cached = await AsyncStorage.getItem('foils');
        if (cached) {
          const products = JSON.parse(cached);
          console.log(`Loaded ${products.length} products from AsyncStorage`);
          return products;
        }
      }
    } catch (error) {
      console.error('Error loading foils data:', error);
    }
    
    console.warn('No foils data found in any location');
    return [];
  }
} 